import { dataGenre } from './API/api';
import Notiflix from 'notiflix';

const galleryBySearch = document.querySelector('#gallery-list');
const baseUrtlImg = 'https://image.tmdb.org/t/p/original/';
const page = 1;
const language = 'en-US';
async function getAllGenres(language) {
  return await dataGenre(language);
}
export async function renderFilmCardsByGenres(genre, data) {
  const genresData = (await getAllGenres(language)).genres;
  let id = genre.id;
  console.log(id);
  console.log(data);
  const filteredRes = [];
  data.results.filter(result => {
    if (result.genre_ids.includes(id)) {
      filteredRes.push(result);
    }
  });
  Notiflix.Notify.success(`Success! We've found some movies for you!!!`);

  const filmList = filteredRes
    .map(
      ({
        id,
        title,
        vote_average,
        genre_ids,
        release_date = 'un',
        poster_path,
      }) => {
        const allGenres = [];
        let imgSrc = null;
        if (poster_path) {
          imgSrc = `${baseUrtlImg}${poster_path}`;
        }
        if (poster_path === null) {
          imgSrc =
            'data:image/png;base64,';
        }
        genresData
          .map(genre => {
            if (genre_ids.includes(genre.id)) {
              allGenres.push(genre.name);
            }
          })
          .join(', ');
        return `<li data-movie-id="${id}" class="card__item">
    <a class="card__link" href = "">
        <img class="card__img"
        src="${imgSrc}"
        alt="${title}"
        loading=lazy
    />

    <h2 class="card__title">${title}</h2>
    <div class="card__text">
        <p class="card__info">${allGenres} | ${release_date
          .split('', 4)
          .join('')}</p>
        <div class="card__rating">${vote_average
          .toString()
          .split('', 3)
          .join('')}</div>
    </div>
    </a>

</li>`;
      }
    )
    .join('');
  galleryBySearch.innerHTML = filmList;
}
